  overflow: auto;

  /*   */
}



.discover {
  padding-bottom: 130px;

  @include desktop {
    padding-bottom: 80px;
  }

  @include tablet {
    padding-bottom: 60px;
  }

  &__content {}

  &__img {
    float: left;
    -webkit-clip-path: polygon(0 0, 100% 0, 100% 100%, 30% 100%);
    clip-path: polygon(0 0, 53% 0%, 31% 100%, 0% 100%);
    shape-margin: 120px;
    shape-outside: polygon(0 0, 53% 0%, 31% 100%, 0% 100%);
    width: 100%;
    height: 750px;

    @include desktop {
      height: 500px;
    }
  }

  &__list {}

  &__item {
    position: relative;
    margin-bottom: 7px;
    padding: 20px;
    height: 78px;
    font-weight: 400;
    font-size: 20px;
    line-height: 130%;
    color: #2d2c3d;

    &:first-child {
      height: 110px;

      @include desktop {
        height: 78px;
      }

      .discover__item-inner::before {
        left: 51%;
      }
    }

    &:nth-child(2) {
      height: 89px;

      @include desktop {
        height: 65px;
      }

      .discover__item-inner::before {
        left: 48%;
      }
    }

    &:nth-child(3) {
      height: 110px;

       .discover__item-inner::before {
        left: 45%;
      }
    }

    &:nth-child(4) {
      height: 78px;

       .discover__item-inner::before {
        left: 42%;
      }
    }

    &:nth-child(5) {
      height: 78px;

       .discover__item-inner::before {
        left: 40%;
      }
    }

    &:nth-child(6) {
      height: 78px;

       .discover__item-inner::before {
        left: 37%;
      }
    }

    &:nth-child(7) {
      height: 78px;

       .discover__item-inner::before {
        left: 35%;
      }
    }

    &:last-child {
      height: 78px;
      font-weight: 600;

       .discover__item-inner::before {
        left: 32%;
      }
    }

  }

  &__item-inner {
    display: inline;

    &::before {
      content: "";
      position: absolute;
      left: 0;
      top: 50%;
      width: 120px;
      height: 2px;
      background: linear-gradient(#fff 0%, #084BC9 100%);
    }
  }
}
